.header {
  display: flex;
  justify-content: space-between;
  border: 1px solid black;
}

.logo {
  width: 100px;
  height: 100px;
  /* border-radius: 50% 50%; */
}

.header .nav-items ul {
  font-size: 20px;
  display: flex;
  list-style-type: none;
}

.header .nav-items ul li {
  padding: 10px;
  margin: 10px;
}

.res-card {
  margin: 10px;
  padding: 5px;
  width: 250px;
  /* height: 290px; */
  border: 1px solid black;
  background-color: rgb(123, 190, 123);
}
.search {
  padding: 5px;
}
.res-card:hover {
  cursor: pointer;
  border-radius: 10px solid black;
}
.res-card p {
  display: grid;
}
.res-card > span {
  display: flex;
  justify-content: space-evenly;
  margin-top: -19px;
}
.res-card > span > h4 {
  text-align: center;
}
.res-card > span > h4:first-child {
  display: flex;
  align-items: center;
  background-color: rgb(19, 176, 1);
  border-radius: 5px;
  padding: 0 5px 0;
  color: white;
}

.res-card > span > h4:first-child .fa-star {
  font-size: small;
  padding: 0 5px 3px 0;
}
.res-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.biryani {
  height: 180px;
  width: 100%;
}

.search {
}

.filter-button {
  margin: 5px;
  cursor: pointer;
}

.shimmer-container {
  display: flex;
  flex-wrap: wrap;
}
.shimmer-cards {
  margin: 20px;
  width: 250px;
  height: 300px;
  background-color: rgb(220, 220, 220);
}

.login {
  height: 30px;
  margin: 15px;
}

.filter {
  display: flex;
}


.card-label{
  position: absolute;
  background-color: black;
  color: white;
  border-radius: 3px 10px;
  margin: 5px;
  margin-top: 5px;
  padding: 4px;
}